<template>


  <component v-if="structure" :is="formLayout" :structure="structure" :model.sync="model"></component>
  <slot></slot>

</template>
<script>
  import LayoutOneColumn from './LayoutOneColumn.vue'
  import LayoutTwoColumn from './LayoutTwoColumn.vue'
  import LayoutThreeColumn from './LayoutThreeColumn.vue'

  export default {
    props: {
      model: {
        type: [Object],
        required: false,
        twoWay: true
      },
      structure: {
        type: [Object],
        required: false,
        default: null
      }
    },
    components: {
      LayoutOneColumn,
      LayoutTwoColumn,
      LayoutThreeColumn
    },
    data () {
      return {
        formLayout: 'LayoutOneColumn'
      }
    },
    created () {
      var numCols = 1
      if (this.structure) {
        for (var key in this.structure) {
          if (this.structure.hasOwnProperty(key)) {
            if (this.structure[key]['col'] && this.structure[key]['col'] > numCols) {
              numCols = this.structure[key]['col']
            }
          }
        }
        switch (numCols) {
          case 2:
            this.formLayout = 'LayoutTwoColumn';
            break
          case 3:
            this.formLayout = 'LayoutThreeColumn';
            break
        }
      }
    },

    methods: {
      validate() {
        alert('111');
      }
    }
  }
</script>



