export default{

  props: {
    config: {
      type: [Object],
      required: false,
      default: function () {
        return {}
      }
    },
    model: {
      type: [Object],
      required: false
    },
    key: {
      type: [String],
      required: true
    },
    required: {
      type: [Boolean, String],
      default: false,
      fill: true,
      toConfig: true
    },
    label: {
      type: [String],
      default: '',
      toConfig: true
    },
    readonly: {
      type: [String, Boolean],
      default: false,
      fill: true,
      toConfig: true
    }
  },

  beforeCompile () {
    if (!this._props) return
    for (let prop of Object.keys(this._props)) {
      let data = this._props[prop]
      if (data.options.fill && data.raw === '') {
        this[prop] = prop
      }
    }
  },

  created () {
    if (!this._props) return
    for (let prop of Object.keys(this._props)) {
      let data = this._props[prop]
      if (data.options.toConfig) {
        this.config[prop] = this[prop]
      }
    }
  }
}



