import TextField from './Text.vue'
import SelectField from './Select.vue'
import SwitchField from './Switch.vue'
import CheckboxField from './Checkbox.vue'
import Badge from 'vuestrap-base-components/src/components/badge'
import UiButton from 'vuestrap-base-components/src/components/buttons'

export const components = {
  TextField,
  SelectField,
  SwitchField,
  CheckboxField,
  Badge,
  UiButton
}

export default {
  install (Vue) {
    Object.keys(components).forEach((name) => {
      Vue.component(name, components[name])
    })
  }
}

export {
  TextField,
  SelectField,
  SwitchField,
  CheckboxField
}



