<template>
  <mdl-select :label="config.label" :default-option="defaultOption" :value.sync="model[key]"
              :options="options"></mdl-select>
</template>

<script>
  import FieldMixin from './mixins/FieldMixin'

  export default {
    mixins: [FieldMixin],
    props: {
      options: {
        type: [Object, Array],
        required: false
      },
      defaultOption: {
        type: String,
        required: false,
        default: ''
      }
    },
    created()
    {
      if (this.config && this.config.options) {
        options = this.config.options
      }
    }
  }
</script>



